import React, {PureComponent} from 'react';
import Head from 'next/head';
import Button from 'antd/lib/button';
import Carousel from 'antd/lib/carousel';
import Icon from 'antd/lib/icon';
import '../style.css';




export default class Home extends PureComponent {
	

	renderHeader = () => {
		return (
				<div className=" theme-bg">
					<div className="framework home-header">
						<div className="home-logo">
							港机销售
						</div>
						<div className="home-contact">
							<span className="mail-icon"><Icon spin={true} style={{'margin-right': '5px', 'font-size': '18px'}} type="mail" />youlikeisthebest@163.com</span>
							<span><Icon spin={true} style={{'margin-right': '5px', 'font-size': '18px'}} type="mobile" />17602170958</span>
						</div>
					</div>
					
					<style jsx>{`
						.home-header {
							color: #fff;
							display: flex;
							justify-content: space-between;
							align-items: center;
						}
						.home-logo {
							font-size: 40px;
							font-family: 'Righteous', cursive;
						}
						.mail-icon {
							padding-right: 10px;
						}
					`}</style>
				</div>
			)
	}
	renderCarousel = () => {
		return (
				<div className="framework">
					<Carousel autoplay>
					    <div className="carousel-img"><img src="/static/img/carousel-1.jpeg" /></div>
					    <div className="carousel-img"><img src="/static/img/carousel-2.jpeg" /></div>
					    <div className="carousel-img"><img src="/static/img/carousel-3.jpeg" /></div>
			    	</Carousel>
			    	<style jsx>{`
			    		.carousel-img {
							height: 400px;
						}
						.carousel-img img {
							width: 100%;
							height: 400px;
						}
		    		`}</style>
				</div>
				
			)

	}

	renderBottom = () => {
		return (
			<div>
				<div className="framework bottom-wrapper">
					<div className="flex-1"> first </div>
					<div className="flex-1"> second </div>
					<div className="flex-1"> third </div>
				</div>
				<style jsx>{`
					.bottom-wrapper {
						color: #000;
					}
				`}</style>
			</div>
		)

	}

	render() {
		return (
			<div>
				<Head>
					<title>home</title>
				    <meta name="viewport" content="initial-scale=1.0, width=device-width" />
				    <link rel='stylesheet' href='https://cdnjs.cloudflare.com/ajax/libs/antd/3.6.3/antd.css'/>
				</Head>
				<div>
					{this.renderHeader()}
					{this.renderCarousel()}
					{this.renderBottom()}

				</div>
				<style jsx>{`
					.home-header {
						display: flex;
						justify-content: space-between;
						align-items: center;
					}
					
				`}</style>
			</div>
		)
	}
}	



